export default function TripMap() {
  let previousCoordinates

  const [stateName, setStateName] = useState(null)

  const onStateSelect = useCallback((stateName) => (event) => {
    setStateName(stateName)
  }, [setStateName])

  return (
    <Box mt={8}>
      <Typography variant='h4' style={{ textAlign: 'center' }}>
        Honeymoon Route
      </Typography>
      <Box mt={4}>
        <Typography>
          <em>
            Hover or click on a state to see the stops in the state.
          </em>
        </Typography>
      </Box>
      <Box style={{ cursor: 'move' }} data-tip="">
        <ComposableMap projection="geoAlbers">
          <ZoomableGroup>
            <Geographies geography={geoUrl}>
              {({ geographies }) =>
                geographies.map(geo => (
                  <Geography
                    key={geo.rsmKey}
                    geography={geo}
                    fill="#DDD"
                    stroke="#FFF"
                    onMouseOver={onStateSelect(geo.properties.name)}
                    onClick={onStateSelect(geo.properties.name)}
                    style={{
                      default: {
                        fill: "#D6D6DA",
                        outline: "none"
                      },
                      hover: {
                        fill: "#F53",
                        outline: "none"
                      },
                      pressed: {
                        fill: "#E42",
                        outline: "none"
                      }
                    }}
                  />
                ))
              }
            </Geographies>
            {markers.map(({ name, coordinates, xOffset, yOffset, hideMarker }, index) => {
              const render = (
                <Fragment key={`${name}-${index}`}>
                  {!hideMarker && (
                    <Marker coordinates={coordinates}>
                      <g
                        fill="none"
                        stroke="#FF8866"
                        strokeWidth="2"
                        strokeLinecap="round"
                        strokeLinejoin="round"
                      >
                        <circle r={4} fill="#FF8866" />
                      </g>
                    </Marker>
                  )}
                  {previousCoordinates && (
                    <Line
                      from={previousCoordinates}
                      to={coordinates}
                      stroke="#FF886666"
                      strokeWidth={2}
                      strokeDasharray='5 10'
                      strokeLinecap="round" />
                  )}
                </Fragment>
              )
              previousCoordinates = coordinates
              return render
            })}
          </ZoomableGroup>
        </ComposableMap>
      </Box>
      {
        markers.find(it => it.stateName === stateName) && (
          <ReactTooltip>
            <Box style={{ fontSize: 18 }}>
              {stateName}
              {markers.filter(it => it.stateName === stateName).map(it => (
                <p style={{ fontSize: 15 }}>
                  {it.name}: {it.date}
                </p>
              ))}
            </Box>
          </ReactTooltip>
        )
      }
    </Box>
  )
}
